
.content{
    display: block;
}
.card{
    /* display: inline-block; */
    /* width: 20vw; */
    height: 60vh;
    margin:1em;
    flex: 1 1 0;
}
.container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
