

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.LandingPage {
  position: relative;
  /* width: 100vw; */
  /* height: 100vh; */
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: 120% auto;
  padding:3rem;
}
.full{
  height: 100vh;
}

.LandingPage .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.LandingPage .content {
  z-index: 1;
  text-align: center;
  color: #fff;
}

.LandingPage h1 {
  font-size: 3rem;
}

.LandingPage p {
  font-size: 1.5rem;
}

.cursor-trail {
  position: absolute;
  width:1.5px;
  height: 1.5px;
  background-color: rgb(255, 255, 255);
  border-radius: 50%;
  animation-name: fadeOut;
  animation-duration: 1.2s;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
  z-index: 2;
}
.stick{
  position: fixed;
  top:0;
  width:100%;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes roll {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 100% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.LandingPage {
  animation: roll 50s linear infinite;
}


@media only screen and (max-width:500px){
  .LandingPage{
    background-size: auto;
  }
  .navbar{
    width: 100vw !important;
  }

}